/// <reference no-default-lib="true"/>

<script lang="ts">
import { Options, Vue } from 'vue-class-component';
import echeckService from '@/service/echeck.service';

@Options({
  props: {
    'xpkg': Object
  },
  methods: {
    download: function(version: any) {
      echeckService.getXPackage(version).then(response => window.open(response.data.downloadUrl))
    }
  }
})
export default class XPackage extends Vue {
  setup() {
    return { echeckService }
  }
}
</script>

<template>
  <div class="pcard">
    <p v-if="xpkg.latest" class="latest">
      Latest Release
    </p>
    <p class="title">
      {{xpkg.fileName}}
    </p>
    <p class="sub-title">
      v{{xpkg.version}} | {{$filters.formatDate(xpkg.releaseDate)}} | {{$filters.formatSize(xpkg.sizeBytes)}}
    </p>
    <button v-on:click="download(xpkg.version)" class="btn" :class="{'btn-primary': !xpkg.latest, 'btn-danger': xpkg.latest }">Download v{{xpkg.version}}</button>
    <img src="images/logo.png" class="logo">
    <img src="images/rhino.png" class="logo rhino-logo">
  </div>
</template>

